import React, { useState } from 'react'
import { connect } from 'react-redux'
import { selectUser, unSelectUser} from '../reducers/selectedUserReducer'

const UsersList = (props) => {

  if (!props.show) {
    return null
  }

  const selectUserHandler = async (event) => {
    console.log(event)
  }

  return (
    <table>
      <tbody>
        <tr>
          <th>username</th>
          <th>name</th>
          <th>blogs created</th>
        </tr>
        {props.users.map(u =>
          <tr key={u._id}>
            <a onClick={() => selectUserHandler(u)}>{u.username}</a>
            <td>{u.name}</td>
            <td>{u.blogs.length}</td>
          </tr>
        )}
      </tbody>
    </table>
  )
}

const mapStateToProps = (state) => {
  return {
    users: state.users
  }
}

export default connect(
  mapStateToProps,
  { selectUser }
)(UsersList)




